<template>
  <Field :type="type"
         :name="name"
         :value="value"
         :rules="rules"
         v-model="mv"
         v-slot="{ field }">

    <input :type="type"
           :id="id"
           :ref="id"
           :name="name"
           :title="title"
           :placeholder="placeholder"
           :readonly="readonly"
           :value="value"
           v-bind="field"
           v-model="mv"
           @input="onInput($event)"
    >

    {{ mv }}
  </Field>

  <ErrorMessage class="form-field-error" :name="name">
  </ErrorMessage>
</template>

<script>
import { Field, ErrorMessage } from 'vee-validate';

export default {
  name: 'ui-text-field',
  components: {
    Field,
    ErrorMessage,
  },
  props: {
    type: String,               // input type
    id: String,                 // input id
    name: String,               // input name
    title: String,              // input title
    placeholder: String,        // input placeholder
    readonly: Boolean,          // input readonly
    rules: String,              // input validation rules
    value: {                    // input value
      type: [String, Number],
      default: undefined,
    },        
    modelValue: {               // input modelValue
      default: '',
    }
  },
  data() {
    return {
      mv: this.modelValue || this.value,
    }
  },
  methods: {
    focus() {
      this.$refs[this.id].$el.focus();
    },
    onInput(e) {
      this.mv = e.target.value;
      this.$emit('update:modelValue', e.target.value);
    },
  },
}
</script>

<style lang="scss" scoped>

</style>